html,body, ::after, ::before, * {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    margin: 0;
    padding: 0;
    color: black;
    font-family: 'Fira Sans', sans-serif;
    background-image: linear-gradient(rgb(194, 194, 194), rgba(130, 130, 189, 0.507), white);

}
.main {
    margin-top: 160px;
    padding: 0 ;

}

.nav {
    box-shadow: 0 1px 13px 2px rgba(5, 5, 5, 0.603);
    position: fixed;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    background-color: #e3e3e3;
    top: 0;
    z-index: 999;
    
    &__items {
        display: flex;
        
    }
    &__img {
        width: 10%;
        opacity: 0.6;
        transition: opacity 500ms ease-in;
        &:hover {
         cursor: pointer;
         opacity: 1;
        }
    }


   &__item {
        border: 2px solid black;
        padding: 6px;
        background-color: white;
        margin: 10px;
        list-style-type: none;
        transition: color 300ms ease-in, background-color 300ms ease-in-out;
        &:hover {
            background-color: black;
            color: white;
            cursor: pointer;
            text-underline-position: below;
        }
    }
}
.hero {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    text-align: center;
    background-size: 100%;
    font-size: 1.5em;
    @media (max-width:800px) {
        font-size: 1em;
    }

   
}
.firstbox {
    align-self: center;
    padding:30px 10px;
    margin: 5px;
    max-width: 600px;
}
.secondbox {
    align-self: center;
    padding:30px 10px;
    margin: 5px;
    max-width: 600px;


}
 
.hero::after {
        background: url(foty/5.jpeg);
        background-position: center;
        background-size: 100%;
        background-repeat: repeat;
        content: "";
        opacity: 0.4;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        z-index: -3;   
    }

.info {
    word-wrap: break-word;
    padding:200px 100px;
    margin: 250px 0;
    background-image: linear-gradient(rgb(96, 152, 226) rgba(126, 126, 233, 0.733));
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: repeat(2, 1fr);
    gap: 50px;

    // &__zero {
    //     grid-row-start: 0;
    //     grid-row-end: 1;
    //     grid-column-start: 1;
    //     grid-column-end: 3;
    //     text-align: center;
    //     padding:0 150px;
    // }
    &__one {
        align-self: center;
        text-align: start;
        grid-row-start: 1;
        padding: 15px;
        grid-column-start: 2;

    }
    &__two {
        align-self: center;
        margin-top: 30px;
        text-align: end;
        padding-inline-start: 15px;
        grid-row-start: 2;
        border: none;
    }
    img {
        max-width: 60%;
        grid-row-start: 1;
        grid-column-start: 1;
        margin: auto;
        border: none;

    }
    #proc {
        grid-row-start: 2;
        grid-column-start: 2;
    }
}
.project_section {
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(rgb(255, 255, 255), rgb(155, 178, 219));
    .project_title {
        font-size: 2em;
        margin: auto;
        margin-top: 100px;
    }

}
.projects {
    display: flex;
    margin: 30px 0;
    margin-bottom: 200px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(rgb(255, 255, 255), rgb(155, 178, 219));

    .card {
        background-image: linear-gradient(rgba(75, 107, 196, 0.856), rgba(95, 106, 109, 0.445));
        margin:20px 5px;
        opacity: 0.6;
        border: none;
        text-align: center;
        padding: 15px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        text-align: right;
        gap: 35px;
        width: 20%;
        transition: box-shadow 500ms ease-in-out, transform 500ms ease-in-out, border 200ms ease-in-out, opacity 500ms ease-in-out;
        &:hover {
            background-color: white;
            cursor: pointer;
            box-shadow: 0 0 72px 4px black;
            transform: scale(1.2);
            opacity: 1;
            z-index: 23;
            ;

        }
        img {
            opacity: 0.7;
        }
        .card-body {
            margin-top: 10px;
        }
    }
}


.footer {
    padding: 0 100px;
    display: grid;
    text-align: center;
    color: whitesmoke;
    grid-template-columns: auto auto;
    grid-template-rows: repeat(2 , 1fr);
    background-color: rgb(54, 54, 54);
    word-break: initial;
    >h2 {
        font-size: 2em;
    }
    &__one  {
        grid-column-start: 1;
        grid-row-start: 1;
        padding-bottom: 30px;
        padding-top: 50px;
    }
    &__two {
        grid-column-start: 2;
        margin: auto;
        grid-column-end: 3;
        grid-row-start: 1;

    }
    &__social {
        border-top: 3px solid black;
        background-color: rgba(219, 217, 217, 0.377) ;
        padding:40px;
        gap: 20px;
        font-size: 1.3em;
        color: black;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        grid-column-start: 1;
        grid-column-end: 4;
        grid-row-start:2 ;
        }
    }
.ul1, .ul2, .ul3 {
    display: flex;
    margin: 0 50px;
    flex-direction: column;
    list-style: none;
    gap: 10px;
}
.ul4 {
    display: flex;
    flex-direction: row;
    align-self: flex-start;
    margin-left: 100px;
    img {
        display: flex;
        margin: 5px;
        width:10%;
        &:hover {
            filter: invert(1);
            cursor: pointer;
        }
    }
}

ul li:first-of-type {
    font-weight: 800;
    font-size: 1.2em;
}